<template>
    <div class="todo">
        <h1>Add New Task</h1>
        <form action="" @submit.prevent="addTask">
            <input
                type="text"
                v-model="taskObj.taskText"
                id=""
                placeholder="Type Your Task"
                required
            />
            <input
                type="date"
                v-model="taskObj.from"
                placeholder="from"
                required
            />
            <input type="date" v-model="taskObj.to" placeholder="to" required />
            <input
                type="submit"
                value="Add Task"
                :disabled="taskObj.taskText.trim() == ''"
            />
        </form>
    </div>
</template>
<script setup>
import { ref } from "vue";
import todoFunction from "@/mixins/todoMixin";
const { tasks, addToLocalSt } = todoFunction();
const taskObj = ref({
    id: "",
    taskText: "",
    from: "",
    to: "",
    createdAt: "",
    completed: false,
});
// Add Task Object to Tasks Array
const addTask = () => {
    taskObj.value.id = tasks.value.length + 1;
    taskObj.value.createdAt = new Date();
    tasks.value.push(taskObj.value);
    addToLocalSt();
    alert("Your Task Was Added Successfully");
    taskObj.value = {
        id: "",
        taskText: "",
        from: "",
        to: "",
        createdAt: "",
    };
};
</script>
<style scoped lang="scss">
form {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin: 50px 20px 10px;
    input[type="text"] {
        flex: 1;
        padding: 10px;
        font-size: 20px;
        &:focus {
            outline: none;
        }
    }
    input[type="submit"] {
        padding: 10px 20px;
        font-size: 20px;
        background-color: #15bdd3bd;
        border: 1px solid #15bdd3bd;
        color: white;
        border-radius: 4px;
    }
}
</style>
